.w1000 {
    margin: 0 auto;
    width: 1000px;
}
.fade {
    // background: #000;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    z-index: 999;
    zoom: 1;
    clear: both;
    _height: 1000px;
    _position: absolute;
    // filter: alpha('opacity:60');
    // @include opacity(0.6);
    filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#99000000', endColorstr='#99000000');background:rgba(0,0,0,0.6);
}
.long-txt-cut {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.tdn {
    text-decoration: none;
    &:hover {
        text-decoration: none;
    }
}
.w-100 {
    width: 100%;
}
.h-100 {
    height: 100%;
}
.fn-hide {
